import React, { useState } from 'react';
import { Button, fade } from '@material-ui/core';
import CmtImage from '../../../../@coremat/CmtImage';
import FundManagerRegistration from '../FundManagerRegistration';
import CmtRevealCard from '../../../../@coremat/CmtRevealCard';
import CmtAdvCardContent from '../../../../@coremat/CmtAdvCard/CmtAdvCardContent';
import makeStyles from '@material-ui/core/styles/makeStyles';


const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    height: '100%',
  },
  revealCard: {
    backgroundColor: fade(theme.palette.primary.main, 0.1),
    height: '100%',
    color: theme.palette.primary.main,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    [theme.breakpoints.up('md')]: {
      paddingLeft: 24,
      paddingRight: 24,
    },
    '& .Cmt-content-head': {
      paddingBottom: 18,
    },
  },
  titleRoot: {
    marginBottom: 16,
  },
  subTitileRoot: {
    fontSize: 14,
    marginBottom: 16,
    letterSpacing: 0.25,
  },
}));

const RegisterFundManager = () => {
  const [revealed, setRevealed] = useState(false);
  const classes = useStyles();

  const revelCard = () => {
    console.log()
    setRevealed(true);
  };

  const handleOnClose = () => {
    setRevealed(false);
  };
  return (
    <CmtRevealCard
      className={classes.root}
      revealComponentTitle="Register as a Nyan Fund Manager"
      revealComponent={<FundManagerRegistration />}
      titleProps={{ component: 'div', variant: 'h4' }}
      revealed={revealed}
      onClose={handleOnClose}>
      <CmtAdvCardContent
        className={classes.revealCard}
        avatar={<CmtImage src={'/images/dashboard/Friend-icon.svg'} />}
        title="Become a Nyan Fund Manager"
        titleProps={{
          variant: 'h3',
          component: 'div',
          className: classes.titleRoot,
        }}
        subTitle="Help manage the Nyan ecosystem and get paid. If you want to run your own pool, go to 'create your own pool'."
        subTitleProps={{
          component: 'p',
          variant: 'subtitle1',
          className: classes.subTitileRoot,
        }}
        extraContent={
          <Button variant="contained" color="primary" onClick={revelCard}>
            Register as a candidate.
          </Button>
        }
        alignCenter
      />
    </CmtRevealCard>
  );
};

export default RegisterFundManager;
