const rows = [
 
];

function createCandidateData(candidate, votes) {
    return { candidate, votes };
}

//get candidates function and populate candidate rows

let candidateRows = [
    
];

const sortCandidates = async () => {
  let candidateRows = [];
  let res = await Contracts.getCandidates();
  let names = res[0];
  let votes = res[1];
  let votingBlock = await Contracts.getNextVotingBlock();
  for (let i = 0; i < names.length; i++) {
    candidateRows.push(createCandidateData(names[i], votes[i]));
  }

  return votingBlock;
}

const voteForCandidate = async (candidate) => {
  console.log(candidate);
}

const useStyles = makeStyles(theme => ({
    padding: {
        marginTop: '30px',
        width: '100%'
    },
    root: {
        backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    modal: {
      minWidth: 275,
      maxWidth: 445,
      margin: '0 auto',
      marginTop: '30px',
      backgroundColor: 'transparent',
    },
    button: {
        margin: theme.spacing(2),
    },
    paper: {
      backgroundColor: theme.palette.background.paper,
      border: '2px solid #000',
      boxShadow: theme.shadows[5],
      padding: theme.spacing(4, 8, 6),
    },
    bullet: {
      display: 'inline-block',
      margin: '0 2px',
      transform: 'scale(0.8)',
    },
    card: {
      backgroundColor: 'white'
    }
}));


export default function FundManagers() {
  const [open, setOpen] = React.useState(false);
  const [nextVotingPeriod, setPeriod] = React.useState(0);
  const [currentBlock, setCurrentBlock] = React.useState(0);
  const [isVotingLive, setVotingLive] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  const classes = useStyles();
  const bull = <span className={classes.bullet}>â€¢</span>;
  useEffect(() => {
    async function loadData() {
      setPeriod(await sortCandidates());
      setCurrentBlock(await Contracts.getCurrentBlock());
      setVotingLive(await Contracts.isFundVotingLive());
    }

    loadData();
  }, []);
  return (
    <PageContainer heading="" breadcrumbs={breadcrumbs}>
        <div>
          <div>
            {isVotingLive ? 
              <div>
                {nextVotingPeriod > currentBlock ? 
                  <div>
                    <Typography variant="h4" gutterBottom>
                      <IntlMessages id={'managers.blocksuntilnextelectionperiod'} /> {nextVotingPeriod - currentBlock}
                    </Typography>
                    <LinearBuffer />
                  </div> : null
                } 
              </div> :
              <div>
                <Typography variant="h1" component="h2" gutterBottom>
                  Registrations are open. Voting will resume shortly.
                </Typography>
                
              </div>
            }
          </div>
            
            <Typography xs={12} className={classes.padding} variant="h1" component="h2" gutterBottom>
              <IntlMessages id={'managers.fundmanagers'} />
            </Typography>
            <TableContainer component={Paper} className={classes.root}>
            <Table aria-label="collapsible table">
                <TableHead>
                <TableRow>
                    <TableCell />
                    <TableCell><IntlMessages id={'managers.manager'} /></TableCell>
                    <TableCell align="right"><IntlMessages id={'managers.allowance'} /></TableCell>
                    <TableCell align="right"><IntlMessages id={'managers.profits'} /></TableCell>
                    <TableCell align="right">ROI</TableCell>
                </TableRow>
                </TableHead>
                <TableBody>
                {rows.map(row => (
                    <Row key={row.name} row={row} />
                ))}
                </TableBody>
            </Table>
            </TableContainer>

            <Typography xs={12} className={classes.padding} variant="h1" component="h2" gutterBottom>
              <IntlMessages id={'managers.candidates'} />
            </Typography>
            {/* <Button variant="contained" onClick={handleOpen}>
              <IntlMessages id={'managers.registercandidate'} />
            </Button> */}
            <RegisterFundManager />
            <TableContainer component={Paper} className={classes.root}>
                <Table className={classes.table} aria-label="simple table">
                    <TableHead>
                    <TableRow>
                        <TableCell><IntlMessages id={'managers.candidatename'} /></TableCell>
                        <TableCell align="right"><IntlMessages id={'managers.votes'} /></TableCell>
                        <TableCell align="right"></TableCell>
                    </TableRow>
                    </TableHead>
                    <TableBody>
                    {candidateRows.map(row => (
                        <TableRow key={row.candidate}>
                            <TableCell component="th" scope="row">
                                {row.candidate}
                            </TableCell>
                            <TableCell align="right">{Formatter.getRoundedBalance(row.votes)}</TableCell>
                            <TableCell align="right">
                                <TextField
                                    id="outlined-number"
                                    label="ETH"
                                    type="number"
                                    InputLabelProps={{
                                        shrink: true,
                                    }}
                                    variant="outlined"
                                />
                                <Button className={classes.button} onClick={voteForCandidate(row.candidate)} variant="contained">Vote!</Button>
                            </TableCell>
                        </TableRow>
                    ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </div>
        
    </PageContainer>
  );
}
